@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe_recipe__OgoFC {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(71, 71, 71);
  margin: 20px;
  display: flex;
  flex-direction: column;
  background: white;
  align-items: center;
  min-width: 40%;
  padding-bottom: 5px;
}

.recipe_image__3s3B0 {
  width: 100%;
  height: 300px;
  padding: 0 20px;
  object-fit: cover;
}
h1 {
  position: relative;
  z-index: 1;
  font-size: 1.2em;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-top: 0px;
  width: 90%;
}
p {
  display: inline;
  padding-left: 5px;
}
.recipe_calories__3wJeH {
  padding-bottom: 5px;
}
.recipe_btn__2Fjbk {
  display: block;
  background: #e58a8a;
  color: #fff;
  position: relative;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  text-align: center;
  padding: 10px;
  transition: 250ms;
  border-radius: 10px;
  margin-top: 20px;
}
.recipe_btn__2Fjbk:hover {
  background: #8db596;
  transition: 250ms;
}
a {
  text-decoration: none;
}
span {
  font-weight: bold;
}

.App {
  min-height: 100vh;
  background-image: linear-gradient(to right, #d7d2cc 0%, #304352 100%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background-color: black;
  border: none;
  color: white;
  padding: 10px 20px;
  font-weight: bold;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
}
li {
  font-size: 1.2em;
  margin-left: 0;
}
ul {
  align-self: flex-start;
}

